import React from 'react';
import Footer from '../Footer';

function About() {
  return (
    <main id="content" className="d-flex">
      <div className="align-self-center mr-lg-5 contentArea">
        <h2>About Me :</h2>
        <hr className="hr mt-0" />
        <p className="h6 d-flex flex-column text-wrap">
          A software engineer with a master's degree in Computer Science from
          Carleton University. I have worked on a diverse set of projects over
          the last 8 years.
        </p>
        <p className="h6 d-flex flex-column text-wrap">
          I am a tenacious developer with strong communication skills and
          work-ethic. I am passionate about contionous learning and writing
          clean code.
        </p>
      </div>
      <Footer />
    </main>
  );
}

export default About;
